* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
    color: inherit;
}

li {
    list-style: none;
}

body {
    line-height: 1;
    color: #333;
}

.header {
    height: 100px;
}

.header .inner {
    max-width: 1280px;
    width: calc(100% - 60px);
    height: 100%;
    margin: 0 auto;

    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav .gnb {
    display: flex;
    gap: 10px;
}

.nav .gnb li a {
    display: block;
    padding: 0 20px;
}

.nav_open {
    cursor: pointer;
    width: 20px;
    position: relative;
    z-index: 9;
}

.nav_open img {
    width: 100%;
}

.all_nav {
    width: 50%;
    height: 100vh;
    position: fixed;
    top: 0;
    right: -100%;
    background-color: skyblue;
    transition: 0.7s;
}

.all_nav.on {
    right: 0;
}

.input_box {
    display: block;
}

.input_box input {
    width: 500px;
    height: 50px;
    padding: 0 20px;
}

.input_box .text {
    margin-top: 20px;
    color: #fff;
}